import React, { useEffect, useState } from "react"
import { Row, Col, Form, Checkbox } from "antd"
import { Link, useHistory, useLocation } from "react-router-dom"
import { Formik } from "formik"
import styled from "styled-components"
import { SubmitButton } from "formik-antd"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import { useMutation } from "@apollo/react-hooks"
import * as yup from "yup"

import { Section, Heading, Button, Alert, Avatar, Logo } from "components"
import TextInput from "components/forms/TextInput"
// import RadioInput from "components/forms/RadioInput"
import { mobile, newClient, renderError } from "helpers"
import { mutateRegistration } from "queries/auth"
import { mutateUserProfileForReferral } from "queries/profile"
import { country, handleChangeLanguage } from "App"
import regisImage from "assets/images/for-regis.svg"
import DynamicIcon from "components/common/DynamicIcon"
import { countries } from "helpers/countryMapping"
import SelectInput from "components/forms/SelectInput"
import { SERVER_BASE_URL } from "helpers/constants"

const BackgroundImage = styled.div`
	background-image: ${`url(${regisImage})`};
	background-size: 25em;
	background-repeat: no-repeat;
	background-position: center;
	position: relative;
	height: 100vh;
	img {
		position: absolute;
		left: 30px;
		top: 20px;
		cursor: pointer;
	}
`

const StyledRow = styled(Row)`
	background-color: #fff;
`

function Signup({ i18n }) {
	const [userProfile, setUserProfile] = useState({})
	const [tc, setTc] = useState(false)

	const history = useHistory()
	const location = useLocation()

	const params = new URLSearchParams(location.search)
	const referralCode = JSON.parse(localStorage.getItem("referral_code")) || {}
	const pathArr = window.location.pathname.split("/")

	const { avatar = [] } = userProfile

	const isFromReferral = params.get("referral") && params.get("code") && params.get("upline")
	const avatarUrl = SERVER_BASE_URL + (avatar[0] || {}).path
	const initialCountry = country !== pathArr[1] ? pathArr[1] : country

	const [fetchProfile] = useMutation(mutateUserProfileForReferral, {
		client: newClient("profile"),
		onCompleted: (data) => {
			data = data.getProfile
			setUserProfile(data)
		}
	})

	const [handleRegis] = useMutation(mutateRegistration, {
		client: newClient("account"),
		onCompleted: (data) => {
			data = data.register || {}
			history.push({
				pathname: `/${country}/register/success`,
				state: { success: true, country: data.country, rolePvid: data.rolePvid }
			})
		},
		onError: (err) => renderError(err)
	})

	const handleRegister = (values, { setSubmitting }) => {
		values = {
			...values,
			firstName: "",
			lastName: "",
			description: "",
			address: "",
			dob: "",
			referralId: Number(params.get("referral")) || 0,
			referralCode: params.get("code") || "",
			uplineId: Number(params.get("upline")) || 0
		}
		const { checkboxes, date, tc, month, year, repeat_password, ...rest } = values
		handleRegis({ variables: { ...rest, referralLink: "" } }).finally(() => {
			setSubmitting(false)
		})
	}

	const referMessage = (
		<span>
			<Trans>You are being referred by</Trans> &nbsp;
			<a
				href={`/${country}/${userProfile.userCode}-${userProfile.pvid}-${userProfile.firstName}-${userProfile.lastName}`}
				target="_blank"
				rel="noopener noreferrer"
			>
				<Avatar src={avatarUrl} size="small" /> {userProfile.firstName} {userProfile.lastName}
			</a>
		</span>
	)

	const handleChangeCountry = (theCountry) => {
		handleChangeLanguage(theCountry, () => {
			window.location.replace(
				isFromReferral
					? `/${theCountry}/register?referral=${referralCode.referral}&code=${referralCode.code}&upline=${referralCode.upline}`
					: `/${theCountry}/register`
			)
		})
	}

	useEffect(() => {
		if (localStorage.getItem("token")) history.push(`/${country}/404`)
		if (isFromReferral) {
			const referralDetails = {
				code: params.get("code"),
				referral: params.get("referral"),
				upline: params.get("upline")
			}
			fetchProfile({ variables: { userPvid: Number(params.get("upline")) } })
			localStorage.setItem("referral_code", JSON.stringify(referralDetails))
		}
		return () => localStorage.removeItem("referral_code")
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [fetchProfile, history, isFromReferral])

	return (
		<StyledRow>
			{!mobile && (
				<Col lg={12}>
					<BackgroundImage>
						<Logo onClick={() => history.push(`/${country}`)} />
					</BackgroundImage>
				</Col>
			)}
			<Col lg={12}>
				{mobile && (
					<Row type="flex" justify="center" className="pt2em">
						<Col lg={12}>
							<Logo onClick={() => history.push(`/${country}`)} />
						</Col>
					</Row>
				)}

				<Section className="pt2em">
					<Heading
						bold
						level={4}
						content={
							<Trans>
								Be counsellor in <strong className="primary">Cudy</strong> today
							</Trans>
						}
						subheader={<Trans>Join our e-counselling platform to start giving advices to those who in needs</Trans>}
					/>
				</Section>

				{isFromReferral && (
					<Section style={{ paddingTop: 0, paddingBottom: 0 }}>
						<Alert type="info" showIcon message={referMessage} />
					</Section>
				)}

				<Section marginBottom={mobile ? "very" : 0} style={{ paddingBottom: !mobile && 0, paddingTop: 0 }}>
					<Formik
						onSubmit={handleRegister}
						// validationSchema={validationSchema(i18n)}
						initialValues={{
							rolePvid: 1,
							email: "",
							password: "",
							repeat_password: "",
							country: initialCountry
						}}
					>
						{({ handleSubmit, values }) => (
							<Form onSubmit={handleSubmit} className="mb4em" layout="vertical">
								<Row gutter={16}>
									<Col lg={24}>
										<TextInput
											label={<Trans>Your email</Trans>}
											type="email"
											name="email"
											placeholder={i18n._(t`Enter your email`)}
										/>
									</Col>
								</Row>
								<Row gutter={16}>
									<Col lg={12}>
										<TextInput
											password
											label={<Trans>Your password</Trans>}
											name="password"
											placeholder={i18n._(t`Enter your password`)}
										/>
									</Col>
									<Col lg={12}>
										<TextInput
											password
											label={<Trans>Repeat password</Trans>}
											name="repeat_password"
											placeholder={i18n._(t`Repeat your password`)}
										/>
									</Col>
								</Row>
								<Section paddingHorizontal={0} className="px0__mobile py0">
									<Row gutter={16}>
										{/* <Col lg={12}>
											<Heading
												content={<Trans>Select your role</Trans>}
												level={4}
												subheader={<Trans>Please choose your role</Trans>}
											/>
											<RadioInput
												name="rolePvid"
												options={[
													{ label: <Trans>Tutor / coach</Trans>, value: 2 },
													{ label: <Trans>Student / parent</Trans>, value: 1 }
												]}
											/>
										</Col> */}

										<Col lg={12}>
											<Heading
												content={<Trans>Select your country</Trans>}
												level={4}
												subheader={<Trans>Choose your preferred country</Trans>}
											/>
											<SelectInput
												name="country"
												onChange={handleChangeCountry}
												options={countries.map(({ key, name, icon }) => ({
													value: key,
													label: (
														<span>
															<DynamicIcon type={icon} size={16} />
															&nbsp; {name}
														</span>
													)
												}))}
											/>
										</Col>
									</Row>
								</Section>
								<Section paddingHorizontal={0} className="px0__mobile">
									<Checkbox name="tc" checked={tc} onChange={() => setTc(!tc)} /> &nbsp;{" "}
									<Trans>
										<span>
											I have agreed to the <Link to="/">terms and conditions</Link> and{" "}
											<Link to="/">privacy policy</Link> of the site
										</span>
									</Trans>
								</Section>
								<Row gutter={24}>
									<Col lg={12} style={{ marginBottom: mobile && "1em" }}>
										<SubmitButton
											block
											size="large"
											type="primary"
											className="mb1em"
											disabled={
												!tc ||
												!values.email ||
												!values.password ||
												!values.repeat_password ||
												values.password !== values.repeat_password
											}
										>
											<Trans>Create account</Trans>
										</SubmitButton>
									</Col>
									<Col lg={12}>
										<Button block type="ghost" htmlType="button" onClick={() => history.push(`/${country}/login`)}>
											<Trans>Login instead</Trans>
										</Button>
									</Col>
								</Row>
							</Form>
						)}
					</Formik>
				</Section>
			</Col>
		</StyledRow>
	)
}

const validationSchema = (i18n) => {
	return yup.object().shape({
		email: yup
			.string()
			.required(i18n._(t`You have to tell your email`))
			.email(i18n._(t`Please provide a valid email address`)),
		password: yup
			.string()
			.required(i18n._(t`You have to fill in your password`))
			.min(8, i18n._(t`Eight characters minimum`)),
		repeat_password: yup
			.string()
			.required("Please repeat your password")
			.oneOf([yup.ref("password")], i18n._(t`Password should match`))
	})
}

export default withI18n()(Signup)
